import React, { useEffect, useRef } from "react";
import G2 from "@antv/g2";
import axios from "axios";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core";

import style from "../../index.module.scss";

const useStyles = makeStyles(theme => ({
  textPrimary: {
    color: theme.palette.primary.main
  }
}));

const SaleTotal = () => {
  const classes = useStyles();
  const ref = useRef("");

  useEffect(() => {
    const colorMap = {
      Asia: G2.Global.colors[0],
      Americas: G2.Global.colors[1],
      Europe: G2.Global.colors[2],
      Oceania: G2.Global.colors[3]
    };
    axios
      .get("https://gw.alipayobjects.com/os/antvdemo/assets/data/bubble.json")
      .then(({ data }) => {
        console.log(data);
        const chart = new G2.Chart({
          container: ref.current,
          forceFit: true,
          height: 450,
          animate: true
        });
        chart.source(data);
        // 
        chart.scale({
          LifeExpectancy: {
            alias: ""
          },
          Population: {
            type: "pow",
            alias: ""
          },
          GDP: {
            alias: "($)"
          },
          Country: {
            alias: "/"
          }
        });
        chart.axis("GDP", {
          label: {
            formatter: function formatter(value) {
              return (value / 1000).toFixed(0) + "k";
            } // 
          }
        });
        chart.tooltip({
          showTitle: false // 
        });
        chart.legend("Population", false); //  Population  Country 
        chart
          .point()
          .position("GDP*LifeExpectancy")
          .size("Population", [4, 65])
          .color("continent", function(val) {
            return colorMap[val];
          })
          .shape("circle")
          .tooltip("Country*Population*GDP*LifeExpectancy")
          .style("continent", {
            lineWidth: 1,
            strokeOpacity: 1,
            fillOpacity: 0.3,
            opacity: 0.65,
            stroke: function stroke(val) {
              return colorMap[val];
            }
          });
        chart.render();
      });
  });

  return (
    <Card className={style.card} style={{ height: 550 }}>
      <CardContent>
        <Typography variant="subtitle1" className={classes.textPrimary}>
          ANALYTICS
        </Typography>
        <Typography variant="subtitle1">TOTAO PROGRAMS SALE</Typography>
        <div ref={ref} />
      </CardContent>
    </Card>
  );
};

export default SaleTotal;
