import React from "react";
import {
  DatePicker,
  Checkbox,
  Select,
  Typography,
  Form,
  Input,
  Row,
  Col,
  Button
} from "antd";

import style from "./index.module.scss";

const { Title, Text } = Typography;
const { TextArea } = Input;
const dateFormat = "YYYY/MM/DD";

const options = [
  { label: "Apple", value: "Apple" },
  { label: "Pear", value: "Pear" },
  { label: "Orange", value: "Orange" },
  { label: "Apple", value: "Apple" },
  { label: "Pear", value: "Pear" },
  { label: "Orange", value: "Orange" },
  { label: "Apple", value: "Apple" },
  { label: "Pear", value: "Pear" },
  { label: "Orange", value: "Orange" },
  { label: "Apple", value: "Apple" },
  { label: "Pear", value: "Pear" },
  { label: "Orange", value: "Orange" }
];

const list = [
  { label: "", value: "Apple" },
  { label: "", value: "Pear" },
  { label: "", value: "Orange" },
  { label: "", value: "Apple1" },
  { label: "", value: "Pear2" },
  { label: "", value: "Orange3" }
];

const BaseInfo = props => {
  const { getFieldDecorator } = props.form;

  const renderCheck = data => {
    const arr = [];
    data.forEach(value => {
      arr.push(
        <Col span={6}>
          <Checkbox value={value.value}>{value.label}</Checkbox>
        </Col>
      );
    });
    return arr;
  };

  const renderList = data => {
    const arr = [];
    data.forEach(value => {
      console.log(value.value);
      arr.push(
        <Col span={8} style={{ paddingRight: 40 }}>
          <Form.Item label={value.label}>
            {getFieldDecorator(value.value, {
              rules: []
            })(<Input />)}
          </Form.Item>
        </Col>
      );
    });
    return arr;
  };

  return (
    <Form>
      <Title level={3}></Title>
      <Text type="secondary">
        Lets begin with the basic introduction to what youre about to offer.
      </Text>
      <Row>
        <Col span={10} style={{ paddingRight: 40 }}>
          <Form.Item label="">
            {getFieldDecorator("project_name", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<Input placeholder="" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("country", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<Select placeholder="" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("project_home", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<Input placeholder="" />)}
          </Form.Item>
        </Col>
        <Col span={10}>
          <Form.Item label="">
            {getFieldDecorator("username", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<Input placeholder="" />)}
          </Form.Item>
          <Form.Item label="/">
            {getFieldDecorator("city", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<Select placeholder="/" />)}
          </Form.Item>
        </Col>
      </Row>
      <Title level={4}></Title>
      <div>
        <Form.Item>
          {getFieldDecorator("describe", {
            rules: [{ required: true, message: "Please input your username!" }]
          })(<TextArea />)}
        </Form.Item>
      </div>
      <Title level={4}></Title>
      <div>
        <Form.Item>
          {getFieldDecorator("tags", {
            rules: [{ required: true, message: "Please input your username!" }]
          })(<Checkbox.Group>{renderCheck(options)}</Checkbox.Group>)}
        </Form.Item>
      </div>
      <Title level={4}></Title>
      <Text type="secondary">
        Let us know when is the last day that participants can register for your
        program!
      </Text>
      <Row>
        <Col span={5} style={{ paddingRight: 40 }}>
          <Form.Item label="">
            {getFieldDecorator("project_start", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<DatePicker format={dateFormat} />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("country", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<DatePicker format={dateFormat} />)}
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item label="">
            {getFieldDecorator("username", {
              rules: [
                { required: true, message: "Please input your username!" }
              ]
            })(<DatePicker format={dateFormat} />)}
          </Form.Item>
        </Col>
      </Row>
      <Title level={4}></Title>
      <div>
        <Form.Item>
          {getFieldDecorator("type", {
            rules: [{ required: true, message: "Please input your username!" }]
          })(<Checkbox.Group>{renderCheck(options)}</Checkbox.Group>)}
        </Form.Item>
      </div>
      <Title level={4}></Title>
      <Row>{renderList(list)}</Row>
      <div style={{ paddingTop: 30 }}>
        <Button type="primary">NEXT STEP</Button>
      </div>
    </Form>
  );
};

export default BaseInfo;
